import axios, { AxiosInstance, AxiosRequestHeaders } from "axios";

// stores an axios instance to the 
class APIService {
    private instance: AxiosInstance;
    private defaultHeaders: AxiosRequestHeaders = {
        "Content-Type": "application/json",
        "Accept": "application/json",
    };

    constructor(url: string) {
        this.instance = axios.create({
            baseURL: url,
            timeout: 5000,
        });
    }

    private getHeaders() {
        return { ...this.defaultHeaders };
    }

    get(url: string, params?: any): Promise<any> {
        return this.instance.get(url, { headers: this.getHeaders(), params,});
    }

    post(url: string, data?: any): Promise<any> {
        return this.instance.post(url, data, { headers: this.getHeaders() });
    }

    patch(url: string, data?: any): Promise<any> {
        return this.instance.patch(url, data, { headers: this.getHeaders() });
    }

    put(url: string, data?: any): Promise<any> {
        return this.instance.put(url, data, { headers: this.getHeaders() });
    }

    delete(url: string): Promise<any> {
        return this.instance.delete(url, { headers: this.getHeaders() });
    }
}

// use this in development
// export default new APIService("http://10.0.0.10:4000");
// use this in production
export default new APIService("/");

const wikiAPI = new APIService("https://en.wikipedia.org/w/api.php?");
export {wikiAPI};