import { Modal, ModalProps, ModalTitle } from "./Modal"
import * as React from 'react';
import { changeUsername } from "../../api/services/game";
import { Player } from "../../models/player.model";
import { Room } from "../../models/game.model";
import theme, { Input, darker, Red } from "../../constants/theme";
import styled from "styled-components";
import { isMobile } from "../../constants/utils";

const ConfirmButton = styled.button`
    float: right;
    background-color: ${theme.colors.primaryColor};
    color: ${theme.colors.primaryTextColor};
    transition: 0.4s;
    font-family: ${theme.fonts.primary};
    font-size: ${isMobile() ? "2rem" : "1.1rem"};
    border-radius: 3px;
    &:hover {
        background-color: ${darker(theme.colors.primaryColor)};
    }
`

export const ChangeUsernameModal = (props: ModalProps & {room: Room, player: Player}) => {
    const [user, setUser] = React.useState<string>("");
    const [error, setError] = React.useState<string>("");
    // const player: Player = useSelector((state: any) => state.player.us);
    // const room: Room = useSelector((state: any) => state.game.currentRoom);
    
    const validateUsername = (username: string): string | boolean => {
        if (username.length < 3) {
            return "Username must be at least 3 characters long";
        }
        if (username.length > 20) {
            return "Username must be less than 20 characters long";
        }
        if (username.match(/[^a-zA-Z0-9_]/)) {
            return "Username must only contain letters, numbers, and underscores";
        }
        // check that no other player in the room has this username
        if (props.room.players.find(p => p.username === username)) {
            return "Username is already in use";
        }
        return true;
    }

    const attemptUsernameChange = () => {
        const check = validateUsername(user);
        if (check === true) {
            changeUsername(props.player, user);
            props.onHide?.();
            setError("");
        }
        else {
            setError(String(check));
        }
    }

    const ref = React.useRef<HTMLInputElement>(null);
    React.useEffect(() => {
        if (props.visible) {
            ref.current?.focus();
        }
    });

    return (
        <Modal visible={props.visible} onHide={() => {
                props.onHide?.();
                ref.current?.blur();
            }}>
            <div>
                <ModalTitle>Change Username</ModalTitle>
                <Input autoFocus placeholder="Username" 
                    onChange={(e) => {setUser(e.target.value)}} 
                    onKeyUp={(e) => {if (e.key === 'Enter') attemptUsernameChange()}}
                    ref={ref}
                />
                <br />

                <ConfirmButton onClick={attemptUsernameChange}>
                    Change
                </ConfirmButton>
                
                <Red>
                    {error}
                </Red>
            </div>
        </Modal>
    )
}