import styled from 'styled-components';
import theme from '../../constants/theme';
import { isMobile } from '../../constants/utils';

const Container = styled.div`
    display: flex;
    background-color: white;
    width: inherit;
    height: auto;
    flex-direction: column;
    position: absolute;
    font-size: ${isMobile() ? "2.5em" : "1.0em"};
`

const Item = styled.div`
    padding-inline: 6px;
    padding-block: 2px;
    max-width: ${isMobile() ? "600px" : "400px"};
    &:hover {
        background-color: ${theme.colors.highlightColor};
    }
`

const Message = styled.div`
    padding-inline: 6px;
    padding-block: 2px;
    font-style: italic;
`

export const SelectList = (props: {items: any[], message: string, onSelect: (term: any) => void}) => {
    return (
        <Container>
            {
                props.message.length > 0 &&
                <Message>
                    {props.message}
                </Message>
            }
            {props.items.map((item, index) => {
                return (
                    <Item key={index} onClick={() => {props.onSelect(item)}}>
                        {item}
                    </Item>
                )
            })
            }
        </Container>
    )
}