import { Room, countReadyPlayers } from '../models/game.model';
import styled from 'styled-components';
import theme, {Red, Green} from '../constants/theme';
import { isMobile } from '../constants/utils';

const Bar = styled.p`
    color: ${theme.colors.secondaryTextColor};
    font-weight: bold;
    font-size: ${isMobile() ? "2.4rem" : "1.7rem"};
    margin-bottom: 20px;
    margin-top: 0px;
`

export const GameInfoBar = (props: {room: Room}) => {
    return (
        <Bar>
            Players: {props.room.players.length >= 3 ? <Green>{props.room.players.length}</Green> : <Red>{props.room.players.length}/3</Red>} in;
            {" "} {/* force a space */}
            {
                (props.room.players.length >= 3 && countReadyPlayers(props.room) >= props.room.players.length - 1) ? <Green>{props.room.players.length - 1}/{props.room.players.length - 1}</Green> :
            <Red>{countReadyPlayers(props.room)}/{props.room.players.length-1}</Red> 
            } ready;
            category: {props.room.category}
        </Bar>
    )
}