import styled from "styled-components";
import theme from "../constants/theme";
import { isMobile } from "../constants/utils";

const LogoImage = styled.img`
    width: 440px;
    margin: auto;
    display: block;
`

const Text = styled.h1`
    font-family: ${theme.fonts.primary};
    text-align: center;
    color: #112;
    font-size: ${isMobile() ? "2.5em" : "1.8em"};
`

const WikiWaitBlock = () => {
    return (
        <>
            <Text>~ The world awaits ~</Text>
            <LogoImage src={require('../assets/wiki-logo.png')} alt="Wiki Logo" />
        </>
    )
}

export { WikiWaitBlock };