import { getAllGames } from "../api/services/game";
import { useEffect } from "react";
import { useState } from "react";
import { Room } from "../models/game.model";

export const GameListPage = () => {
    const [games, setGames] = useState<Room[]>([]);

    useEffect(() => {
        getAllGames().then((data: any) => {
            console.log(data);
            setGames(data.rooms);
        });
    }, []);

    return (
        <div>
            <h1>Game List Page</h1>
            <ul>
                {games.map((game: Room) => (
                    <li key={game.uuid}>
                        <a href={`/game/${game.uuid}`}>{game.uuid}</a>
                        <ul>
                            <li>
                                <b>{game.players.length} Player(s):</b> {game.players.map((player: any) => player.username).join(", ")}
                            </li>
                            <li>
                                Category: {game.category}
                            </li>
                            <li>
                                Status: {game.isInRound ? "In Round" : "Waiting for players"}
                            </li>
                        </ul>
                    </li>
                ))}
            </ul>
            {
                games.length === 0 &&
                <p>No games found</p>
            }
        </div>
    )
}