import { Modal, ModalProps, ModalTitle } from "./Modal"
import { SelectList } from "../inputs/SelectList"
import * as React from 'react';
import { debounce } from "../../constants/utils";
import { searchCategories } from "../../api/services/articles";
import api from "../../api";
import { Input } from "../../constants/theme";
import { Room } from "../../models/game.model";

export const OptionsModal = (props: ModalProps & {room: Room}) => {
    const [searchResults, setSearchResults] = React.useState<any[]>([]);
    const [noResults, setNoResults] = React.useState<boolean>(false);
    const search = debounce(async (term: string) => {
        if (term.length > 0) {
            const res = await searchCategories(term);
            if (res.length === 0) {
                setNoResults(true);
            }
            else {
                setNoResults(false);
            }
            setSearchResults(res);
        }
        else {
            setSearchResults([]);
            setNoResults(false);
        }
    });
    
    return (
        <Modal onShow={props.onShow} onHide={props.onHide} visible={props.visible}>
            <ModalTitle>
                Game Options 
            </ModalTitle>
            <p>Category:</p>
            <div>
                <Input type="text" placeholder='category' onChange={(e) => {search(e.target.value)}} onFocus={(e) => {search(e.target.value)}} />
                <SelectList items={searchResults} message={noResults ? "Nothing matched!" : ""} onSelect={async (term: any) => {
                    await api.patch(`/api/game/room/${props.room.uuid}`, {category: term});
                    props.onHide?.();
                    setSearchResults([]); // empty the results.
                }} />
            </div>
        </Modal>
    )
}