import styled from "styled-components";
import theme, { darker, lighter } from "../../constants/theme";
import { isMobile } from "../../constants/utils";

const ActionButtonStyle = styled.button`
    background-color: ${theme.colors.primaryColor};
    color: ${theme.colors.primaryTextColor};
    border-style: solid;
    border-width: ${isMobile() ? "6px" : "3px"};
    border-right-color: ${theme.colors.tertiaryColor};
    border-bottom-color: ${theme.colors.tertiaryColor};
    border-left-color: ${lighter(theme.colors.tertiaryColor)};
    border-top-color: ${lighter(theme.colors.tertiaryColor)};
    font-family: ${theme.fonts.primary};
    font-weight: 900;
    font-size: ${isMobile() ? "2.1em" : "1.25em"};
    margin: ${isMobile() ? "20px" : "10px"};
    padding: 2px;
    padding-inline: 8px;
    border-radius: ${isMobile() ? "6px" : "3px"};
    transition: background-color 0.3s ease-in-out;


    &:hover {
        background-color: ${darker(theme.colors.primaryColor)};
    }
    &:active {
        border-left-color: ${theme.colors.tertiaryColor};
        border-top-color: ${theme.colors.tertiaryColor};
        border-right-color: ${lighter(theme.colors.tertiaryColor)};
        border-bottom-color: ${lighter(theme.colors.tertiaryColor)};
    }
    &:disabled {
        opacity: 0.6;
        background-color: ${theme.colors.primaryColor};
        border-left-color: ${lighter(theme.colors.tertiaryColor)};
        border-top-color: ${lighter(theme.colors.tertiaryColor)};
        border-right-color: ${theme.colors.tertiaryColor};
        border-bottom-color: ${theme.colors.tertiaryColor};
    }
`

export const ActionButton = (props: { children: any, onClick: () => void, disabled?: boolean }) => {
    return (
        <ActionButtonStyle onClick={props.onClick} disabled={props.disabled}>
            {props.children}
        </ActionButtonStyle>
    );
}