import { fakePlayer, Player } from "./player.model";

export interface Room {
    uuid: string;
    guesserIndex: number;
    articleIndex: number;
    players: Player[];
    category: string;
    isInRound: boolean;
    lastStartTime: number;
}

const fakeGame: Room = {
    uuid: "fake",
    guesserIndex: 1,
    articleIndex: 0,
    isInRound: false,
    players: [fakePlayer],
    category: "fake",
    lastStartTime: 0
}

export { fakeGame };

export const countReadyPlayers = (room: Room) => {
    return room.players.filter((player: Player, index: number) => index !== room.guesserIndex && player.chosenArticle).length;
}
